import { gql } from '@apollo/client';

// 获取用户信息
export const USER_SHOW = gql`
  query userShow($id: ID!) {
    user_show(_id: $id) {
      user_name
      art_science
      eju_subject
      birthday
      gender
      region
      degree
      japanese_ability {
        certificate
        point
      }
      english_ability {
        certificate
        point
      }
      goal_school
      eju_point {
        subject
        point
      }
    }
  }
`;
// 登录
export const USER_LOGIN = gql`
  query userLogin($email: String!, $password: String!) {
    user_login(email: $email, password: $password) {
      _id
    }
  }
`;

// 注册
export const USER_RIGISTER = gql`
  query addUserRegister(
    $email: String!
    $password: String!
    $userName: String!
  ) {
    addUser_register(email: $email, password: $password, user_name: $userName) {
      _id
    }
  }
`;

// 保存用户信息
export const USER_COMPLETE = gql`
  mutation addUserInfo(
    $artScience: String!
    $ejuSubject: String!
    $birthday: String
    $region: String
    $degree: String
    $japaneseAbilityCertificate: String
    $japaneseAbilityPoint: Int
    $englishAbilityCertificate: String
    $englishAbilityPoint: Int
    $goalSchool: String
    $ejuPoint: Int
  ) {
    addUser_adduserinfo(
      art_science: $artScience
      eju_subject_1: $ejuSubject
      birthday: $birthday
      region: $region
      degree: $degree
      japanese_ability_certificate: $japaneseAbilityCertificate
      japanese_ability_point: $japaneseAbilityPoint
      english_ability_certificate: $englishAbilityCertificate
      english_ability_point: $englishAbilityPoint
      goal_school: $goalSchool
      eju_point: $ejuPoint
    ) {
      _id
    }
  }
`;

// 修改登录密码
export const CHANGE_PWD = gql`
  mutation changPwd($id: String!, $password: String) {
    changePW(_id: $id, password: $password) {
      _id
    }
  }
`;

// 查询用户消息
export const QUERY_INFORMATION = gql`
  query getInformation($id: ID!) {
    information(_id: $id) {
      _id
      date
      time
      title
      content
    }
  }
`;

// 用户反馈
export const CREAT_FEEDBACK = gql`
  mutation creatFeedback(
    $id: String!
    $fbType: String
    $fbContent: String
    $fbImage: String
  ) {
    createFbEmail(
      _id: $id
      fb_type: $fbType
      fb_content: $fbContent
      fb_image: $fbImage
    ) {
      _id
    }
  }
`;
