import React, { useEffect } from 'react';
import { WhiteSpace, Steps, List, InputItem } from 'antd-mobile';
import { useHistory } from 'react-router-dom';
import { useLazyQuery } from '@apollo/client';
import { USER_SHOW } from '@/helpers/APIHelper';
import { Page, PageTitle, PrimaryButton } from '../styles';

const { Step } = Steps;
const CompleteShow: React.FC = () => {
  const history = useHistory();
  const [getUserInfo, { loading, data }] = useLazyQuery(USER_SHOW);
  console.log('data++++=', data);
  useEffect(() => {
    getUserInfo({ variables: { id: '111' } });
  }, [getUserInfo]);
  return (
    <>
      <Page>
        <PageTitle>请完善您的信息</PageTitle>
        <WhiteSpace size="xl" />
        <Steps direction="horizontal" current={2}>
          <Step />
          <Step />
          <Step />
        </Steps>
        <WhiteSpace size="xl" />
        <List>
          <InputItem editable={false} value="孜然肉片" labelNumber={7}>
            昵称:
          </InputItem>
          <InputItem editable={false} value="理科" labelNumber={7}>
            文理:
          </InputItem>
          <InputItem editable={false} value="物理" labelNumber={7}>
            考学科目:
          </InputItem>
          <InputItem editable={false} value="2020-01-01" labelNumber={7}>
            出生日期:
          </InputItem>
          <InputItem editable={false} value="男" labelNumber={7}>
            性别:
          </InputItem>
          <InputItem
            editable={false}
            value="河南省，开封市，龙亭区"
            labelNumber={7}
          >
            国内地区:
          </InputItem>
          <InputItem editable={false} value="高中" labelNumber={7}>
            最终学历:
          </InputItem>
          <InputItem editable={false} value="高级" labelNumber={7}>
            日语能力:
          </InputItem>
          <InputItem editable={false} value="初级" labelNumber={7}>
            英语能力:
          </InputItem>
          <InputItem editable={false} value="东京大学" labelNumber={7}>
            目标大学:
          </InputItem>
          <InputItem editable={false} value="666分" labelNumber={7}>
            留考最高成绩:
          </InputItem>
          <WhiteSpace size="xl" />
        </List>
        <PrimaryButton
          type="primary"
          onClick={(): void => history.push('/library')}
        >
          确认
        </PrimaryButton>
        <WhiteSpace size="xl" />
        <WhiteSpace size="xl" />
      </Page>
    </>
  );
};

export default CompleteShow;
